.Content {
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: row;
  width: 100%;
}

.Chart {
  width: 50%;
}

.Chart td,
.Chart th {
  border: 1px solid #444;
  padding: 8px;
}

.Chart tr:nth-child(even) {
  background-color: #f2f2f2;
}

.Chart tr:hover {
  background-color: #ddd;
}

.Chart th {
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: #496bbe;
  color: white;
}



.Wrong {
  border: 2px solid red;
}

.Right {
  border: 2px solid springgreen;
}

.Normal {
  border: 2px solid #444;
}

.Hidden {
  display: none;
}

.Switch-Chart {
  margin: 10px;
  font-size: 50px;
  color: #444;
}

.Options {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Chart-Option {
  margin: 10px;
  width: 40%;
  font-size: 25px;
}
